.btn {
  text-decoration: none;
  display: inline-block;
  padding: 1em;
}
.btn_primary {
  color: white;
  background-color: lighten($color: #000000, $amount: 0.1);
}

.button_row {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
  margin-bottom: 40px;
  &_content {
    align-content: center;
  }
  a {
    width: 150px;
    margin-right: 20px;
    text-align: center;
    font-weight: 500;
    &:last-child {
      margin-right: 0;
    }
  }
}

.compare_title {
  margin: auto;
  text-align: center;
  margin-top: 40px;
}
.btn_selected {
  background-color: orange;
  color: black;
}
